.Left{
    width: 5rem;
    height: 100vh;
    margin-left: 3.5rem;
    padding-top: 35vh;
    position: fixed;
    top: 0;
    left: 0;
}

.Left > div:not(.L-scroll){
    width: 2.5rem;
    height: 0.3rem;
    border-radius: 0.5rem;
    margin: 3rem 0;
    opacity: 1;
    background: var(--color-white);
    cursor: pointer;
    transition: all 350ms ease;
}
.Left > div:hover:not(.L-scroll){
    opacity: 0.7;
}
.Left > div.active{
    width: 100%;
}

.L-scroll{
    margin-top: 8rem;
    margin-right: 3rem;
}

.L-scroll > p{
    font-size: 1.4rem;
    transform: rotate(90deg);
    letter-spacing: 0.3rem;
}

.L-scroll > div{
    font-size: 1.4rem;
    width: 0.3rem;
    height: 12rem;
    border-radius: 0.5rem;
    margin-top: 11rem;
    margin-left: 0.9rem;
    background: var(--color-white);
}